
h1 {
	font-size: 4.7em;
}
.header-container {
	padding: 70px;
	padding-left: 20%;
	background-color: #4ebdef;
}
.header-container * {
	color: #fff;
}
#mainContent{
	padding: 25px 10%;
	text-align: center;
}
#mainContent>div>div{
	border: 1px #b6deee solid;
	border-radius: 10px;
	background: #a5e2fa;
	padding: 10px;
	font-size: 16px;
	line-height: 25px;
}
#mainContent>div>div>h2{
	padding-bottom: 10px;
}

